



















import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import accountSelect from '@/components/common-components/accountSelect'
import wx from 'weixin-js-sdk'
import { formatDate } from '@/until/index'

@Component({
  components: {
    accountSelect
  },
  filters: {
    fenToYuan (num: any) {
      if (typeof num !== 'number' || isNaN(num)) return null
      return '¥' + (num / 100).toFixed(2)
    }
  }
})
export default class payAdmin extends Vue {
  raceLength = 0
  raceCost = 0
  accountList = []
  accountId = ''

  // 计算属性
  get computedMsg () {
    return 'computed ' + this.propB
  }
  get accountOptions () {
    this.accountList.forEach((obj) =>{
      obj.value = obj.id
      obj.text = obj.name
    })
    return this.accountList
  }
  getRaceByAccountId () {
    const toast = this['$createToast']({
      time: 0,
      txt: '数据获取中...'
    })
    toast.show()
    this.$store.dispatch('getRaceByAccountId', { accountId: this.accountId }).then((response) => {
      if (response.success) {
        let resData = response.data
        if (resData.raceLength) {
          this.raceLength = resData.raceLength
          this.raceCost = resData.raceCost
        }
      }
      toast.hide()
    })
  }

  getAccountId (id: any) {
    this.accountId = id
    this.getRaceByAccountId()
  }
  toOtherPage (type: any) {
    if (type) {
      let path = '/' + type
      this.$router.push({ path: path, query: { accountId: this.accountId} })
    }
  }
}
